.slider-container {
  @extend .container;
  @include for-phone-only {
    // На мобильной версии у слайдера может не быть падингов
    padding: unset;
  }
  @include for-desktop-up {
  }
}

.slider {
  @include for-tablet-up {
    max-width: 428px;
    margin-left: unset;
    margin-right: auto;
  }
  @include for-desktop-up {
    max-width: 548px;
  }
}

.swiper {
  &-wrapper {
    @extend %list;
    margin-bottom: 40px;
    @include for-tablet-up {
      margin-bottom: 32px;
    }
  }
  &-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    max-width: 245px;
    max-height: 245px;

    transition: ease-in-out 0.5s;

    // box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    // Тени убрали сознательно
    border-radius: 2px;

    @include for-tablet-up {
      max-width: 194px;
      max-height: 194px;
    }
    @include for-desktop-up {
      min-width: 254px;
      min-height: 254px;
    }
  }
  &-inner {
    width: 100%;
  }
}

.swiper-btns {
  height: 32px;
  position: relative;
  display: block;
}

.swiper-btn {
  width: 32px;
  height: 32px;
}

.swiper-icon--white {
  fill: $primaryLight;
}

.button {
  border: 1px solid transparent;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.02em;
  padding: 0;
  cursor: pointer;
  text-align: center;
  background-color: transparent;
}

.swiper-button {
  &-prev {
    @extend .button;
    left: 15px;
    &::after {
      content: "";
    }
  }
  &-next {
    @extend .button;
    left: 79px;
    &::after {
      content: "";
    }
  }
}
